*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 62.5%;

}
body{
  background: rgb(3, 0, 31);
  color: white;
  font-family:' Poppins',sans-serif;

}
.App{
  min-height: 100vh;
  display: flex;

}
.sideBar{
  flex: 3;
  border-right: 1px solid rgb(100,100,100);
}
.main{
  flex: 9;
}
.logo{
  margin-right: 1rem;
}
.brand{
  font-size: 2rem;
}
.upperSide{
  padding: 5rem;
  border-bottom: 1px solid rgb(100,100,100);
  height: 70%;
}
.upperSideTop{
  display: flex;
  align-items: center;
  margin-bottom: 2.5rem;
}
.addBtn{
  height: 2rem;
  padding-right: 1rem;
}
.midBtn{
  background: #584BFF;
  border: none;
  color: white;
  padding: 1.5rem;
  font-size: 1.5rem;
  width: 25rem;
  display: flex;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 3rem;
  border-radius: 0.5rem;

}   
 .query{
  display: flex;
  align-items: center;
  background: transparent;
  color: rgba(222,222,222,1);
  padding: 1.5rem;
  width: 25rem;
  margin: 1rem auto;
  border-radius:0.5rem ;
  border: 1px solid rgba(98,98,98,1);

}
.query>img{
  margin-right: 2rem;
  object-fit: cover;
  height: 2rem;
}

.lowerSide{
  padding: 2rem;
}

.listItems{
  margin: 1rem;
  display: flex;
align-items: center;
font-size: 1.3rem;

}
.listItemsImg{
  margin: 1rem;
}

.listItems:nth-child(1)>.listItemsImg{
  margin-right: 0.5rem;
}
.listItems:nth-child(3)>.listItemsImg{
  margin-right: 0.7rem;
}
.main{
  min-height: calc(100vh-14rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 6rem 10 rem;
  margin-bottom: 0;
}
.chats{
  overflow: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  width: 100%;
  max-width: 70rem;
  height: calc(100vh - 17rem);
  margin-top: auto;
}
.chat{
  margin: 1rem;
  padding: 2rem 3rem;
  font-size: 1.3rem;
  display: flex;
  align-items: flex-start;
  text-align: justify;
}
.chatFooter{
  margin-top: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  margin-bottom: 20rem;
}
.inp{
  padding: 0.5rem;
  background: rgba(28,30,58,1);
  display:flex;
  align-items: center;
  border-radius: 0.5rem;
  width: 68rem;
  margin-left: 22rem;
  align-items: flex-start;



  
}
.inp>input{
  width: calc(100% - 3rem);
  outline: none;
  padding: 1.25rem;
  color: white;
}
.inp>input, .send{
background: transparent;
border:none
}

.chatFooter{
  margin: 2rem 0;
}
.chatimg{
  object-fit: cover;
  width: 5rem;
  margin-left: 2rem;
  border-radius: 0.5rem;
}
.txt{
  margin-left: 2rem;
margin-top: 1rem;

}

.bot{
  background: rgba(38,30,58,1);
  width: fit-content;
  border-radius: 0.5rem;
}
.sendimg{
margin-top: 1rem;
}
.txt{
  line-height: 2.4rem;
  color: rgba(222,222,222,1);
}

